@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --header-height: 3rem;
  --font-semi: 600;
}
:root {
  --first-color: #4070f4;
  --second-color: #0e2431;
}
:root {
  --body-font: "Poppins", sans-serif;
  --big-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
  --title-font-size: 1.4rem;
  --subtitle-font-size: 1rem;
  --small-font-size: 0.6rem;
}
@media screen and (min-width: 768px) {
  :root {
    --big-font-size: 3.5rem;
    --h2-font-size: 2rem;
    --normal-font-size: 1rem;
    --title-font-size: 1.6rem;
    --subtitle-font-size: 1.2rem;
    --small-font-size: 0.8rem;
  }
}
:root {
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}
*,
::before,
::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: "Poppins", sans-serif;
  font-family: var(--body-font);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  color: #0e2431;
  color: var(--second-color);
}
h1,
h2,
p {
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
.section-title {
  position: relative;
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  color: #4070f4;
  color: var(--first-color);
  margin-top: 1rem;
  margin-top: var(--mb-2);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
  text-align: center;
}
.section-title::after {
  position: absolute;
  content: "";
  width: 64px;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2rem;
  background-color: #4070f4;
  background-color: var(--first-color);
}
.section {
  padding-top: 3rem;
  padding-bottom: 2rem;
}
.bd-grid {
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-left: var(--mb-2);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}
.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(146, 161, 176, 0.15);
}
.nav {
  height: 3rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  font-weight: var(--font-semi);
}
@media screen and (max-width: 768px) {
  .nav__menu {
    position: fixed;
    top: 3rem;
    top: var(--header-height);
    right: -100%;
    width: 80%;
    height: 100%;
    padding: 2rem;
    background-color: #0e2431;
    background-color: var(--second-color);
    transition: 0.5s;
  }
}
.nav__item {
  display: block;
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}
.nav__link {
  position: relative;
  color: #fff;
}
.nav__link:hover {
  position: relative;
  cursor: pointer;
}
.nav__link:hover::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: #4070f4;
  background-color: var(--first-color);
}
.nav__logo {
  color: #4070f4;
  color: var(--first-color);
}
.nav__logo:hover {
  cursor: pointer;
}
.nav__toggle {
  color: #0e2431;
  color: var(--second-color);
  font-size: 1.4rem;
  font-size: var(--title-font-size);
  cursor: pointer;
}
.active::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: #4070f4;
  background-color: var(--first-color);
}
.show {
  right: 0;
}
.home {
  height: calc(100vh - 3rem);
  grid-row-gap: 1rem;
  row-gap: 1rem;
}
.home__data {
  align-self: center;
}
.home__title {
  font-size: 2rem;
  font-size: var(--big-font-size);
  margin-bottom: 2.5rem;
  margin-bottom: var(--mb-5);
}
.home__title-color {
  color: #4070f4;
  color: var(--first-color);
}
.home__social {
  display: flex;
  flex-direction: column;
}
.home__social-icon {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
  font-size: 1.4rem;
  font-size: var(--title-font-size);
  color: #0e2431;
  color: var(--second-color);
}
.home__social-icon:hover {
  color: #4070f4;
  color: var(--first-color);
}
.home__img {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 295px;
  z-index: -1;
}
.animate_headline {
  text-transform: uppercase;
}
.button {
  display: inline-block;
  background-color: #4070f4;
  background-color: var(--first-color);
  color: #fff;
  padding: 0.75rem 2.5rem;
  font-weight: 600;
  font-weight: var(--font-semi);
  border-radius: 0.5rem;
}
.rotate_180,
.rotate_360 {
  transition: transform 0.8s ease-in-out;
}
.button:hover {
  cursor: pointer;
  box-shadow: 0 10px 36px rgba(0, 0, 0, 0.15);
}
.button:hover .rotate_180 {
  transform: rotate(180deg);
}
.button:hover .rotate_360 {
  transform: scale(1.4) rotate(360deg);
}
.about__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
  padding-top: 1%;
  text-align: center;
}
.about__subtitle {
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}
.about__img {
  justify-self: center;
}
.about__img img {
  width: 800px;
  border-radius: 0.9rem;
  box-shadow: 0 10px 36px rgba(0, 0, 0, 0.15);
}
.about__text {
  font-size: 1rem;
  font-size: var(--subtitle-font-size);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}
.work__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
  text-align: center;
}
.work__subtitle {
  font-size: 1.4rem;
  font-size: var(--title-font-size);
  margin-bottom: 0.05rem;
  text-align: center;
}
.work__subtitle_position {
  font-size: 1rem;
  font-size: var(--subtitle-font-size);
  color: #4070f4;
  color: var(--first-color);
  text-align: center;
}
.work__subtitle_date {
  font-size: 0.6rem;
  font-size: var(--small-font-size);
  margin-bottom: 0.8rem;
  text-align: center;
  text-decoration: underline;
}
.work__text {
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}
.project__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
  text-align: center;
}
.project__subtitle {
  font-size: 1rem;
  font-size: var(--subtitle-font-size);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
  text-align: center;
}
.project__text {
  font-size: 0.6rem;
  font-size: var(--small-font-size);
  color: #4070f4;
  color: var(--first-color);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
  text-align: center;
  font-style: italic;
}
.project__img {
  transition: 1s;
  cursor: pointer;
  box-shadow: 0 4px 25px rgba(69, 69, 245, 0.1);
  padding: 0.5rem;
  border-radius: 1.6rem;
  height: 140px;
}
.project__img:hover {
  transform: scale(1.1);
  box-shadow: 0 4px 25px rgba(69, 69, 245, 0.5);
}

.skills__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
  text-align: center;
}
.skills__subtitle {
  color: black;
  padding: 0.75rem 0.5rem;
  font-weight: 600;
  font-weight: var(--font-semi);
}
.skills__text {
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}
.skills__data {
  display: flex;
  width: 33%;
  justify-content: space-between;
  align-items: center;
  position: relative;
  font-weight: 600;
  font-weight: var(--font-semi);
  padding: 0.5rem 1rem;
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
  border-radius: 0.5rem;
  box-shadow: 0 4px 25px rgba(14, 36, 49, 0.15);
}

.skills__names {
  display: flex;
  align-items: center;
}
/* .button-group{
  box-shadow: 0 4px 25px rgba(14,36,49,.15);
} */
.skills__img {
  /* box-shadow: 0 4px 25px rgba(14,36,49,.15); */
  border-radius: 0.5rem;
  overflow: hidden;
  margin: 18px 0;
}

.skill-image {
  margin-left: auto;
  margin-right: auto;
  width: 5rem;
}

.gallery-menu {
  text-align: center;
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.footer {
  background-color: black;
  color: #fff;
  text-align: center;
  font-weight: 600;
  font-weight: var(--font-semi);
  padding: 2rem 0;
}
.footer__social {
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}
.footer__icon {
  font-size: 1.4rem;
  font-size: var(--title-font-size);
  color: #fff;
  margin: 0 1rem;
  margin: 0 var(--mb-2);
}

.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 10000; /* Sit on top */
  padding-top: 4rem; /* Location of the box */
  left: 0;
  top: 0;
  overflow: auto; 
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 1s;
  animation-name: animatetop;
  animation-duration: 1s;
  width: 100vw;
  height: 100vh;
}

/* Modal Content */
.modal-content { 
  background-color: #fff;
  margin: auto;
  padding: 10px 16px;
  border: 1px solid #4070f4;
  border: 1px solid var(--first-color);
  border-radius: 0.6rem;
  width: 98%;
}
/* Add Animation */

@keyframes animatetop {
  from {
    top: -500px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
.modal__title {
  color: black;
  font-weight: 600;
  font-weight: var(--font-semi);
  font-size: 1.4rem;
  font-size: var(--title-font-size);
  margin-bottom: 2px;
}
.modal__close {
  float: right;
  font-size: 1.4rem;
  font-size: var(--title-font-size);
  color: red;
  transition: transform 0.8s ease-in-out;
}
.modal__close:hover {
  cursor: pointer;
  transform: rotate(360deg) scale(1.4, 1.4);
}
.modal__subtitle {
  color: black;
  font-weight: 0.938rem;
  font-weight: var(--normal-font-size);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  margin-bottom: 3rem;
  margin-bottom: var(--mb-6);
  font-style: italic;
}

.modal__text {
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.modal-btn {
  display: inline-block;
  background-color: #4070f4;
  background-color: var(--first-color);
  color: #fff;
  padding: 0.5rem 1rem;
  font-weight: 600;
  font-weight: var(--font-semi);
  border-radius: 0.5rem;
}

.blue__it {
  color: #4070f4;
  color: var(--first-color);
  font-style: italic;
  font-weight: bold;
}

.scrollTop {
  padding: 0.75rem 1.5rem;
  background-color: #4070f4;
  background-color: var(--first-color);
  color: #fff;
  position: fixed;
  right: 30px;
  bottom: 30px;
  height: 40px;
  border-radius: 0.5rem;
  z-index: 1000;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: 1s;
  opacity: 0.8;
}

.scrollTop:hover {
  opacity: 1;
  transform: scale(1.4);
  animation: bounce 1.8s infinite;
  -webkit-animation: bounce 2.5s infinite;
  -moz-animation: bounce 2.5s infinite;
  -o-animation: bounce 2.5s infinite;
}
@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-16px);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.8;
  }
}

@media screen and (min-width: 768px) {
  body {
    margin: 0;
  }
  .section {
    padding-top: 4rem;
    padding-bottom: 3rem;
  }
  .section-title {
    margin-bottom: 3rem;
    margin-bottom: var(--mb-6);
  }
  .section-title::after {
    width: 80px;
    top: 3rem;
  }

  .nav {
    height: calc(3rem + 1rem);
    height: calc(var(--header-height) + 1rem);
  }
  .nav__list {
    display: flex;
    padding-top: 0;
  }
  .nav__item {
    margin-left: 3rem;
    margin-left: var(--mb-6);
    margin-bottom: 0;
  }
  .nav__toggle {
    display: none;
  }
  .nav__link {
    color: #0e2431;
    color: var(--second-color);
  }

  .home {
    height: 100vh;
  }
  .home__data {
    align-self: flex-end;
  }
  .home__social {
    padding-top: 0;
    padding-bottom: 2.5rem;
    flex-direction: row;
    align-self: flex-end;
  }
  .home__social-icon {
    margin-bottom: 0;
    margin-right: 2rem;
    margin-right: var(--mb-4);
  }
  .home__img {
    width: 50%;
    bottom: 15%;
  }

  .about__container,
  .work__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: left;
    text-align: initial;
  }
  .project__container {
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    text-align: left;
    text-align: initial;
  }
  .about__img img {
    width: 300px;
  }
  /* .work__container{
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2,1fr);
    column-gap: 2rem;
  } */
  .skills__container {
    grid-template-columns: repeat(6, 1fr);
    /* grid-template-rows: repeat(2,1fr); */
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
  }
  .modal-content {
    width: 90%;
  }
}
/* @media screen and (max-width: 768px){
  .modal-content{
    width: 98%;
  }
} */

@media screen and (min-width: 1024px) {
  .bd-grid {
    margin-left: auto;
    margin-right: auto;
  }
  .home__img {
    right: 12%;
    width: 46%;
  }
  .modal-content {
    width: 60%;
  }
}

@media screen and (min-width: 1366px) {
  .modal-content {
    width: 40%;
  }
}

